import { createAsyncThunk } from '@reduxjs/toolkit';
import { API_URL, InternalAPI, SubRoutes } from 'common/constants';

// GET DASHBOARD DATA THUNK
export const getDashboardData = createAsyncThunk('dashboard/getDashboardData', async ({ pageno, limit }) => {
    const requestOptions = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({ pageNumber: pageno, pageLimit: limit })
    };
    return fetch(`${API_URL}${InternalAPI.DASHBOARD}${SubRoutes.LIST}`, requestOptions).then((res) => res.json());
});
